import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { StyledGrid, StyledArt } from './StyledAbout';
import { Section, Container } from '@components/global';
const About = () => (
  <StaticQuery
    query={graphql`
      query {
        art_certification: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "certification" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_screen: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "screen" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_chat_box: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "chat_box" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="about">
        <Container>
          <StyledGrid>
            <div>
              <h2>交付讓您不再擔憂的網頁應用產品</h2>
              <p>
                我們瞭解想要找到值得長期託付的網頁應用開發團隊非常困難，十六進位要求工程師交付的網站程式碼品質要達到最高標準。這代表我們的產品更適合長期維護、讓未來接手的團隊更加容易修改。
              </p>
            </div>
            <StyledArt>
              <Img fluid={data.art_certification.childImageSharp.fluid} />
            </StyledArt>
          </StyledGrid>
          <StyledGrid inverse>
            <StyledArt>
              <Img fluid={data.art_screen.childImageSharp.fluid} />
            </StyledArt>
            <div>
              <h2>協助您打造更加實際的產品</h2>
              <p>
                十六進位的開發團隊有數位擁有豐富開發經驗的工程師負責監督。我們會提供您實際開發上的顧問服務，幫你釐清真正的目標以及打造真正能夠幫助你達成目標的產品。
              </p>
            </div>
          </StyledGrid>
          <StyledGrid>
            <div>
              <h2>我們追求最高品質</h2>
              <p>
                十六進位不同於以往傳統接案公司，沒有辦公室，降低並壓縮公司成本費用，反將成本利潤分配給合作夥伴，我們相信推動工程師的不是夢想，是錢，所以讓工程師及設計師收入最大化，讓工程師願意花更多心力在產品上，最大化產品的品質。
              </p>
            </div>
            <StyledArt>
              <Img fluid={data.art_chat_box.childImageSharp.fluid} />
            </StyledArt>
          </StyledGrid>
        </Container>
      </Section>
    )}
  />
);

export default About;
