import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Container } from '@components/global';
import {
  StyledHeaderWrapper,
  StyledArt,
  StyledGrid,
  StyledText,
} from './StyledHeader';
const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        art_website: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "website" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1400) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <StyledHeaderWrapper>
        <Container>
          <StyledGrid>
            <StyledArt>
              <Img fluid={data.art_website.childImageSharp.fluid} />
            </StyledArt>
            <StyledText>
              <h2>
                十六進位追求
                <br />
                客戶獲得最高品質的程式
                <br />
                工程師獲得成長的機會
              </h2>
            </StyledText>
          </StyledGrid>
        </Container>
      </StyledHeaderWrapper>
    )}
  />
);

export default Header;
