import React from 'react';
import { ExternalLink } from '@common';
import { StaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

import {
  StyledFooterWrapper,
  StyledContainer,
  StyledCopyright,
  StyledSocialIcons,
} from './StyledFooter';
const SOCIAL = [
  {
    icon: faGithub,
    link: 'https://github.com/hexadecimalCo',
  },
];
const Footer = () => (
  <StaticQuery
    query={graphql`
      query {
        art_pot: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "customers_pot" }
        ) {
          childImageSharp {
            fluid(maxWidth: 960) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={() => (
      <StyledFooterWrapper>
        <StyledContainer>
          <StyledCopyright>
            <h5>
              Copyright ©2020 Hexadecimal Industrial Networking Corp. All Rights
              Reserved.
            </h5>
          </StyledCopyright>
          <StyledSocialIcons>
            {SOCIAL.map(({ icon, link }) => (
              <ExternalLink key={link} href={link}>
                <FontAwesomeIcon icon={icon} />
              </ExternalLink>
            ))}
          </StyledSocialIcons>
        </StyledContainer>
      </StyledFooterWrapper>
    )}
  />
);
export default Footer;
