import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { Section } from '@components/global';
import Img from 'gatsby-image';
import {
  StyledContainer,
  StyledArt,
  StyledUl,
  StyledCaseContent,
} from './StyledCase';
import caseArr from './caseArr';
const Case = () => (
  <StaticQuery
    query={graphql`
      query {
        art_story: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "tell_story" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="case" accent>
        <StyledContainer>
          <StyledArt>
            <Img fluid={data.art_story.childImageSharp.fluid} />
          </StyledArt>
          <StyledCaseContent>
            <h2>合作案</h2>
            <StyledUl>
              {caseArr.map(caseObj => (
                <li key={caseObj.title}>
                  <a href={caseObj.link}>{caseObj.title}</a>
                </li>
              ))}
            </StyledUl>
            <p>還有更多更多應用後台...</p>
          </StyledCaseContent>
        </StyledContainer>
      </Section>
    )}
  />
);

export default Case;
