import React from 'react';
import { useForm } from 'react-hook-form';
import { sentForm } from './fetchData';
import {
  StyledContainer,
  StyledSection,
  StyledInputContainer,
  StyledInputBox,
  StyledErrorMessage,
} from './StyledContactUs';
const Input = ({ label, name, register, required, type, errors, ...props }) => (
  <StyledInputContainer>
    <StyledInputBox isTextarea={type === 'textarea'}>
      <label>{label}：</label>
      {type === 'textarea' ? (
        <textarea name={name} ref={register({ required })} {...props} />
      ) : (
        <input name={name} ref={register({ required })} {...props} />
      )}
    </StyledInputBox>
    {errors[name] && <StyledErrorMessage>此為必填</StyledErrorMessage>}
  </StyledInputContainer>
);
const ContactUs = () => {
  const { register, handleSubmit, reset, errors } = useForm();
  const onSubmit = data => {
    sentForm(data);
    // reset()
  };
  return (
    <StyledSection id="contact-us">
      <StyledContainer>
        <iframe
          style={{
            margin: '0 auto',
            width: '100%',
          }}
          src="https://docs.google.com/forms/d/e/1FAIpQLSeCWT-eS0GCjdTSMhLuK3wb2jL5ZEn4JQDHP8CqIZf2ZOKGyQ/viewform?embedded=true"
          width="640"
          height="914"
          frameborder="0"
          marginheight="0"
          marginwidth="0"
        >
          載入中…
        </iframe>
      </StyledContainer>
    </StyledSection>
  );
};

export default ContactUs;
