import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Section } from '@components/global';
import {
  StyledTeamGrid,
  StyledArt,
  StyledArtMobile,
  StyledTitle,
  StyledSubtitle,
  StyledContainer,
  StyledImg,
} from './StyledTeam';
const TEAM = [
  {
    name: '蔡世恩',
    image: '4n.jpg',
    role: 'cofounder',
    major: '前端工程師',
  },
  {
    name: '劉澔暐（懷恩）',
    image: 'whien.jpg',
    role: 'cofounder',
    major: '軟體工程師',
  },
  {
    name: '林尹麒（引擎）',
    image: 'engine.jpg',
    role: 'cofounder',
    major: '前端工程師',
  },
];

const TeamMember = ({ name, role, img, major }) => {
  return (
    <div key={name}>
      <StyledImg fluid={img.childImageSharp.fluid} alt={name} />
      <StyledSubtitle>
        {role} - {major}
      </StyledSubtitle>
      <StyledTitle>{name}</StyledTitle>
    </div>
  );
};

const Team = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "team" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
        art_yellow_duck: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "yellow_duck" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="team" accent="secondary">
        <StyledContainer>
          <h2>團隊成員</h2>
          <StyledTeamGrid>
            {TEAM.map(({ name, image, role, major }) => {
              const img = findTargetImageFileFrom(data, image);
              return (
                <TeamMember
                  name={name}
                  role={role}
                  img={img}
                  major={major}
                  key={image}
                />
              );
            })}
          </StyledTeamGrid>
          <StyledArt>
            <Img fluid={data.art_yellow_duck.childImageSharp.fluid} />
          </StyledArt>
          <StyledArtMobile>
            <Img fluid={data.art_yellow_duck.childImageSharp.fluid} />
          </StyledArtMobile>
        </StyledContainer>
      </Section>
    )}
  />
);

export default Team;

const findTargetImageFileFrom = (data, targetImagePath) =>
  data.allFile.edges.find(({ node }) => node.relativePath === targetImagePath)
    .node;
